.post {
    display: flex;
    flex-direction: column;
    gap: 20px;
    border-radius: 30px;
    margin-top: 20px
}

.postsContainer {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.container {
    padding: 30px 20px;
    border-radius: 20px;
    color: white;
    background: #171719;
}

::-webkit-resizer {
    display: none;
}

h3 {
    margin-bottom: 20px;
    font-size: 2.3rem;
}

textarea {
    resize: none;
    width: 100%;
    color: white;
    border-radius: 10px;
    padding: 5px 2px;
    border: none;
    background: #272727;
    font-size: 16px;
}

textarea::placeholder {
    color: white;
}

textarea:focus {
    outline: 0;
}

.postTextAreaContainer {
    display: flex;
    border-radius: 10px;
    position: relative;
    margin-bottom: 20px;
}

.postTextAreaContainer button{
    background: rgba(0, 0, 0, 0);
    border: none;
    border-radius: 10px;
    min-width: 40px;
}

.postTextAreaContainer button:hover {
    background: #f51dd0;
}

.sendIcon {
    cursor: pointer;
    color: white;
    /*background: #f51dd0;*/
}

.sendIcon svg {
    background: none;
}
