.profileInfoContainer {
    position: relative;
    border-radius: 0 0 20px 20px;
    padding-bottom: 10px;
    background: #171719;
}

.picB {
    overflow: hidden;
    height: 200px;
    margin-bottom: 85px;
    background-position: center center;
    background-size: cover;
}

.picBimg {
    width: 100%;
    object-fit: contain;
}

.profileInfo {
    margin: 10px;
    padding: 0 10px;
}

.profileInfoTop {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    align-items: center;
}

.editButton {
    border: none;
    background: rgba(200, 26, 170, 0.89);
    padding: 8px 10px;
    border-radius: 10px;
    font-size: 16px;
    cursor: pointer;
    color: white;
    transition: 0.4s ease;
}

.editButton:hover {
    background: #f51dd0;
}

.profileInfoName {
    font-size: 29px;
    color: white;
}

.profilePic {
    position: absolute;
    background-position: center center;
    background-size: cover;
    width: 130px;
    height: 130px;
    display: block;
    top: 150px;
    left: 20px;
    border-radius: 50%;
    border: 5px solid #171719;
    z-index: 2;
    overflow: hidden;
}

.picEditButton {
    position: absolute;
    width: 100%;
    top: 100%;
    border: none;
    background: rgba(0, 0, 0, 0.61);
    padding: 5px 10px;
    border-radius: 10px;
    font-size: 15px;
    cursor: pointer;
    color: white;
    transition: 0.4s ease;
    text-align: center;
}

.profilePic:hover > .picEditButton {
    top: 79%;
}

.statusText {
    color: white;
    font-size: 1.1rem;
    font-weight: bold;
}

.picInput {
    display: none;
}

.editButtonContainer {
    position: relative;
}

.editButtonMenu {
    position: absolute;
    background: #f51dd0;
    display: flex;
    flex-direction: column;
    gap: 5px;
    padding: 10px;
    border-radius: 10px;
    font-size: 15px;
}

.statusInput {
    height: 21px;
    width: fit-content;
    background: #111112;
    color: white;
    font-size: 1.1rem;
    font-weight: bold;
    border: none;
}

.statusContainer {
    width: fit-content;
}