.nav {
    width: 18%;
}

.navContainer {
    display: flex;
    flex-direction: column;
    font-size: 20px;
    font-weight: 500;
    border-radius: 20px;
    padding: 10px;
    margin: 20px 20px 20px 0;
    background: #171719;
}

.item {
    padding: 10px 0;
    border-radius: 20px;
}

.itemLink {
    padding: 5px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    transition: 0.1s ease;
}

.itemLinkP {
    transition: 0.4s ease;
}

.itemLink:hover {
    color: #f51dd0;
}

.itemLink:hover > .itemLinkP {
    color: #f51dd0;
}

.itemLink p {
    margin-left: 10px;
}

.item a {
    color: #fff;
    text-decoration: none;
}

.item:active, .item:focus {
    background: rgba(255, 255, 255, 0.13);
}