.item {
    color: #ffffff;
    display: flex;
    gap: 15px;
    align-items: center;
    flex-direction: column;
    min-width: 130px;
}

.item img {
    width: 80px;
    height: 100%;
    background-position: center center;
    background-size: cover;
    display: block;
    border-radius: 50%;
}

.postItem {
    padding: 30px 20px;
    border-radius: 20px;
    display: flex;
    gap: 5px;
    color: white;
    background: black;
}

.itemContent {
    font-size: 18px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}

.itemUsername {
    font-size: 18px;
    font-weight: 300;
}