h1 {
    color: white;
    text-align: center;
    font-size: 4rem;
    margin-bottom: 2rem;
    padding-top: 100px;
}

form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
}

input {
    outline: none;
    width: 380px;
    height: 48px;
    border-radius: 12px;
    border: 3px solid #86868b
}

.menuSelector {
    outline: none;
    width: 180px;
    height: 48px;
    border: none;
    background: rgba(200, 26, 170, 0.89);
    padding: 5px;
    border-radius: 10px;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
    color: white;
    transition: 0.4s ease;
}

.menuSelector:hover {
    transform: scale(1.05);
}

.menuSelector:disabled {
    background: rgba(200, 26, 170, 0.62);
    cursor: no-drop;
}

.loginInputError {
    border: 3px solid red;
}

.loginInput {
    background: #61dafb;
}

p{
    color: white;
}

.green {
    background: green;
    height: 500px;
}

.checkBox {
    width: 20px;
    height: 20px;
}

.loginButtons {
    display: flex;
    justify-content: center;
    gap: 20px;
}

.loginContainer {
    display: flex;
    flex-direction: column;
}

.rememberMe {
    display: flex;
    align-items: center;
    gap: 10px;
}